import { useState } from "react";

function Panel(props){
    const {items, person, addItem, readonly, removeItem, priceList} = props;
    const total = items.map(x => +x.points).reduce((p, c) => p + c, 0);
    const [points, setPoints] = useState(undefined);
    const [reason, setReason] = useState("");
    const save = () => {
        if(points > 0){
            addItem({points, reason});
            setPoints(0);
            setReason("");
        }
    }
    return <div style={{width: "50%", paddingLeft: "10px", paddingRight: "10px"}}>
        <h2>{person}{priceList && <span onClick={priceList} style={{cursor: 'pointer', marginLeft: "10px"}}>(Price list)</span>}</h2>
        <ul>
            {items.map((i, index) => <li style={{display: 'flex', flexDirection:'row', justifyContent: 'space-between'}}>
                <div><span style={{width: "30px", display: 'inline-block'}}>{i.points}</span> <span style={{marginLeft: "10px"}}>{i.reason}</span></div> 
                <p onClick={() => removeItem(index)} style={{cursor: "pointer"}}>x</p>
            </li>)}
        </ul>
        <p style={{marginBottom: '6px'}}>Total: {total}</p>
        {!readonly && <div style={{display: "flex", flexDirection: "row"}}>
            <input 
                type="number" 
                value={points} 
                onChange={(e) => setPoints(e.target.value)} 
                step={1}
                style={{width: "20%"}} />
            <input type="text" value={reason} onChange={(e) => setReason(e.target.value)} style={{width: "70%"}}/>
            <button onClick={save} style={{width: "10%"}}>+</button>
        </div>}
    </div>
}

export default Panel;