import logo from './logo.svg';
import './App.css';
import Panel from './Panel';
import { useEffect, useState } from 'react';
import { checkPassword } from './userManager';
import { getItems, getPriceList, updateItems, updatePriceList } from './store';
import { PriceList } from './PriceList';

const corey = "Corey";
const sylwia = "Sylwia";
const naughty = "Naughty";
const nice = "Nice";

function App() {
  // get the list of items
  const [coreyNaughtyItems, setCoreyNaughtyItems] = useState([]);
  const [coreyNiceItems, setCoreyNiceItems] = useState([]);
  const [sylwiaNaughtyItems, setSylwiaNaughtyItems] = useState([]);
  const [sylwiaNiceItems, setSylwiaNiceItems] = useState([]);
  const [coreyPriceList, setCoreyPriceList] = useState([]);
  const [sylwiaPriceList, setSylwiaPriceList] = useState([]);

  const [password, setPassword] = useState("");
  const [loggedIn, setLoggedIn] = useState(false);
  const [plOpen, setPlOpen] = useState("");
  const removeItem = (non, person) => {
    return (index) => {
      let items;
      
      let setter; 
      if(non == naughty && person == corey){setter = setCoreyNaughtyItems; items = coreyNaughtyItems}
      if(non == nice && person == corey){setter = setCoreyNiceItems; items = coreyNiceItems}
      if(non == naughty && person == sylwia){setter = setSylwiaNaughtyItems; items = sylwiaNaughtyItems}
      if(non == nice && person == sylwia){setter = setSylwiaNiceItems; items = sylwiaNiceItems}
      const newItems = items.filter((x, i) => i != index);
      setter(newItems);
      updateItems(non + ":" + person, newItems);
    }
  }
  const removePlItem = (person) => {
    return (index) => {
      let items;
      let setter;
      if(person == corey){setter = setCoreyPriceList; items = coreyPriceList};
      if(person == sylwia){setter = setSylwiaPriceList; items = sylwiaPriceList};
      const newItems = items.filter((x,i) => i != index);
      setter(newItems);
      updateItems(person, newItems);
    }
  }
  const addItem = (non, person) =>{
    return (item) => {
      let items;
      
      let setter; 
      if(non == naughty && person == corey){setter = setCoreyNaughtyItems; items = coreyNaughtyItems}
      if(non == nice && person == corey){setter = setCoreyNiceItems; items = coreyNiceItems}
      if(non == naughty && person == sylwia){setter = setSylwiaNaughtyItems; items = sylwiaNaughtyItems}
      if(non == nice && person == sylwia){setter = setSylwiaNiceItems; items = sylwiaNiceItems}
      const newItems = [...items, item];
      setter(newItems);
      updateItems(non + ":" + person, newItems);
    }
  }
  const addPlItem = (person) => {
    return (item) => {
      let items;
      let setter;
      if(person == corey){setter = setCoreyPriceList; items = coreyPriceList};
      if(person == sylwia){setter = setSylwiaPriceList; items = sylwiaPriceList};
      const newItems = [...items, item];
      setter(newItems);
      updatePriceList(person, newItems);
    }
  }
  const loadItems = () => {
    getItems(naughty + ":" + corey).then(setCoreyNaughtyItems)
    getItems(naughty + ":" + sylwia).then(setSylwiaNaughtyItems)
    getItems(nice + ":" + corey).then(setCoreyNiceItems)
    getItems(nice + ":" + sylwia).then(setSylwiaNiceItems)
    getPriceList(corey).then(setCoreyPriceList);
    getPriceList(sylwia).then(setSylwiaPriceList);
  }
  const login = (pw) =>{
    if(checkPassword(pw)){
      window.localStorage.setItem("pw", pw);
      setLoggedIn(true);
      setPassword("");
      loadItems();
      setInterval(() => {
        loadItems();
      }, 1000 * 20);
    }
  }
  useEffect(() => {
    var lsPassword = window.localStorage.getItem("pw");
    if(lsPassword && lsPassword.length){
      setTimeout(() => login(lsPassword), 0);
    }
  }, [])
  const plClose = () => {setPlOpen("");}
  const panelSectionStyle = {display: 'flex', flexDirection: 'column'};
  const panelsStyle = {display: 'flex', flexDirection: 'row'};
  return (
    <div className="App">
      {plOpen != "" && <PriceList 
        person={plOpen}
        onClose={plClose}
        addItem={(i) => addPlItem(plOpen)(i)}
        removeItem={(i) => removePlItem(plOpen)(i)} 
        items={plOpen == corey ? coreyPriceList : sylwiaPriceList}/>}
      <header className="App-header">
        <p>Naughty or Nice?</p>
        <p>Corey  Sylwia</p>
      </header>
      {loggedIn ? <main>
        <div style={panelSectionStyle}>
          <h1>{naughty}</h1>
          <div style={panelsStyle}>
          <Panel 
            person={sylwia} 
            items={sylwiaNaughtyItems}
            addItem={addItem(naughty, sylwia)}
            removeItem={removeItem(naughty, sylwia)}/>
          <Panel 
            person={corey} 
            items={coreyNaughtyItems}
            addItem={addItem(naughty, corey)}
            removeItem={removeItem(naughty, corey)}/>
        </div>
        </div>
        <div style={panelSectionStyle}>
          <h1>{nice}</h1>
          <div style={panelsStyle}>
          <Panel 
            priceList={() => setPlOpen(sylwia)}
            person={sylwia} 
            items={sylwiaNiceItems}
            addItem={addItem(nice, sylwia)}
            removeItem={removeItem(nice, sylwia)}/>
          <Panel 
            priceList={() => setPlOpen(corey)}
            person={corey} 
            items={coreyNiceItems}
            addItem={addItem(nice, corey)}
            removeItem={removeItem(nice, corey)}/>
          </div>
        </div>
      </main> : <main style={{display: "flex", alignItems: "center", justifyContent: 'center'}}>
          <form style={{display: "flex", flexDirection: "column"}}>
            <input type="password" value={password} onChange={(e) => setPassword(e.target.value)}/>
            <button onClick={() => login(password)}>Login</button>
          </form>
        </main>}
    </div>
  );
}

export default App;
