import { useState } from "react";

export function PriceList(props){
    const {items, addItem, removeItem, person, onClose} = props;
    console.log(items);
    const [price, setPrice] = useState(0);
    const [desc, setDesc] = useState("");

    const save = () => {
        if(price > 0 && desc.length){
            const item = {price, desc};
            addItem(item);
            setPrice(0);
            setDesc("");
        }
    }
    return <Modal title={person + "'s price list"} onClose={onClose}>
         <ul>
            {items.map((x, i) => <li style={{display: "flex", justifyContent: "space-between"}}>
                <div style={{display: "flex"}}>
                    <p style={{width: "120px"}}>{x.price}</p>
                    <p>{x.desc}</p>
                </div>
                <p onClick={() => removeItem(i)}>x</p>
            </li>)}
            <div>
                <input style={{width: "19%"}} type="number" value={price} onChange={(e) => setPrice(e.target.value)}/>
                <input style={{width: "69%"}} type="text" value={desc} onChange={(e) => setDesc(e.target.value)}/>
                <button style={{width: "9%"}} onClick={save}>+</button>
            </div>
         </ul>
    </Modal>
}

function Modal(props){
    const {children, title, onClose} = props;
    return <div class="modal-overlay" onClick={(e) => onClose()}>
        <div class="modal" onClick={(e) => e.stopPropagation()}>
            <div class="modal-header">
                <p>{title}</p>
                <p onClick={() => onClose()}>x</p>
            </div>
            <div class="modal-body">
                {children}
            </div>
        </div>
    </div>
}