import KVdb from 'kvdb.io';


const bucket = {
    async get(key){
        return await fetch("https://kvdb.io/7LXEVjKmbJtRfSdfgu6vgT/" + key, {
            cache: 'no-cache'
        }).then(x => x.json()).catch(x => []);
    },
    async set(key, items){
        return await fetch("https://kvdb.io/7LXEVjKmbJtRfSdfgu6vgT/" + key, {
            method: "PUT",
            body: JSON.stringify(items)
        })
    }
}
export async function getItems(key){
    return storeToRaw(await bucket.get(key));
}

export async function updateItems(key, items){
    await bucket.set(key, rawToStore(items));
}

export async function getPriceList(key){
    return (await bucket.get("pl:" + key))?.map(x => 
    ({
        price: x.p,
        desc: x.d
    })
    ) || [];
}

export async function updatePriceList(key, items){
    await bucket.set("pl:" + key, items.map(x => ({p: x.price, d: x.desc})));
}

function rawToStore(items){
    return items.map(x => ({
        p: x.points,
        r: x.reason
    }));
}

function storeToRaw(items){
    return items ? items.map(x => ({
        points: x.p,
        reason: x.r
    })) : [];
}